import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConfig } from '../../app/app.config';
import 'rxjs/add/operator/map';
/**
 * Generated class for the MessagePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-message',
  templateUrl: 'message.html',
})
export class MessagePage {
  url = AppConfig.getUrl() + "/djySpider/GetMoreActivities?type=3&cookie=" + localStorage.djycookie
  arr: any;
  pageNo = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, public alertCtrl: AlertController, public loadingCtrl: LoadingController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MessagePage');
    this.get()
  }

  get(event?) {
    if (event) {
      this.pageNo++;
    } else {
      this.pageNo = 0;
      this.arr = [];
    }
    let loading = this.loadingCtrl.create({
      content: '加载中...'
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.http.get(this.url + "&pg=" + this.pageNo)
        .map(res => res.json())
        .subscribe(data => {
          resolve(data);
          if (event)
            event.complete();
          if (data.status) {
            for (let i = 0; i < data.data.data.length; i++) {
              this.arr.push({
                id: data.data.data[i].url.substr(data.data.data[i].url.indexOf("?tid=")+5),
                title: data.data.data[i].title1,
                cover: data.data.data[i].cover,
               // sign: data.data.data[i].sign.substring(data.data.data[i].sign.indexOf("\"jgdzb\">") + 8, data.data.data[i].sign.indexOf("</em>")),
                time: data.data.data[i].time,
              })
            }
            console.log(this.arr)
          } else {
            let alert = this.alertCtrl.create({
              title: data.msg
            });
            alert.present();
          }
        }, err => {
          let alert = this.alertCtrl.create({
            title: err
          });
          alert.present();
        })
      loading.dismiss();
    })
  }
  goto(id,title) {
    this.navCtrl.push("MessarticlePage", { id: id,title:title })
  }
}



